import React, {useRef, useEffect} from "react";
import anime from "animejs";
import {OptionsNav} from "../styles/MenuOptionsStyles";
const MenuOptions = ({clickMenu}) => {
    // animaciones
    const Navbar = useRef()
    useEffect(() => {
        clickMenu ? 
        anime({
            targets: Navbar.current,
            translateX: ["-100%"],
            opacity: [0, 1],
            delay: function(el, i, l) {
              return i * 100
            },
            duration: 1000,
            easing: 'easeInBounce'
          })
          :
          anime({
            targets: Navbar.current,
            translateX: ["0"],
            opacity: [0, 1],
            duration: 1500,
            easing: 'easeOutBounce'
          })
    }, [clickMenu])
    return(
        <OptionsNav ref={Navbar} >
            <p>
                Hola
            </p>
            <p>
                Bebe
            </p>
            <p>
                Carlos
            </p>
        </OptionsNav>
        )
}
export default MenuOptions;